@charset "UTF-8";
/*Глобальные стили*/
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Lato";
  font-display: swap;
  src: url("../fonts/lato/lato-regular.woff2") format("woff2"), url("../fonts/lato/lato-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Oswald";
  font-display: swap;
  src: url("../fonts/oswald/oswald-regular.woff2") format("woff2"), url("../fonts/oswald/oswald-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Oswald";
  font-display: swap;
  src: url("../fonts/oswald/oswald-medium.woff2") format("woff2"), url("../fonts/oswald/oswald-medium.woff") format("woff");
}
.container {
  width: 100%;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 20px;
  max-width: 1300px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 30px;
  }
}
@media (min-width: 1024px) {
  .container {
    padding: 0 40px;
  }
}

html {
  height: 100%;
}

body {
  display: flex;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  font-family: "Oswald", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

a {
  text-decoration: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

/*Стили кнопок, текстов, заголовков*/
.main-title {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  font-family: "Oswald", "Arial", sans-serif;
  color: #000000;
}
@media (min-width: 768px) {
  .main-title {
    font-size: 60px;
    line-height: 60px;
  }
}

.index-sub-title {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  font-family: "Oswald", "Arial", sans-serif;
  color: #000000;
}
@media (min-width: 768px) {
  .index-sub-title {
    font-size: 60px;
    line-height: 60px;
  }
}

.sub-title {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 24px;
  line-height: 26px;
  font-family: "Oswald", "Arial", sans-serif;
  color: #000000;
}
@media (min-width: 768px) {
  .sub-title {
    font-size: 36px;
    line-height: 36px;
  }
}

.product-title {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  font-family: "Oswald", "Arial", sans-serif;
  color: #000000;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .product-title {
    font-size: 20px;
    line-height: 24px;
  }
}
@media (min-width: 1024px) {
  .product-title {
    line-height: 21px;
  }
}

.button {
  display: block;
  padding: 11px;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  font-family: "Oswald", "Arial", sans-serif;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #68b738;
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .button {
    padding: 10px 26px;
    font-size: 20px;
    line-height: 20px;
  }
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active, .button:focus {
  background-color: #5eaa2f;
  color: rgba(255, 255, 255, 0.3);
}
.button:disabled {
  opacity: 0.3;
  pointer-events: none;
}

.button-pagination {
  display: block;
  padding: 10px;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  font-family: "Oswald", "Arial", sans-serif;
  text-align: center;
  color: #444444;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #f2f2f2;
  border: none;
  cursor: pointer;
}
.button-pagination:hover {
  background-color: #ebebeb;
}
.button-pagination:active, .button-pagination:focus {
  background-color: #ebebeb;
  color: rgba(0, 0, 0, 0.3);
}
.button-pagination:disabled {
  opacity: 0.3;
  pointer-events: none;
}

.text {
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  font-family: "Lato", "Arial", sans-serif;
  color: #444444;
}
@media (min-width: 768px) {
  .text {
    font-size: 16px;
    line-height: 24px;
  }
}

.input-text {
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  font-family: "Oswald", "Arial", sans-serif;
  color: #444444;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .input-text {
    font-size: 20px;
    line-height: 30px;
  }
}

/*Блоки страниц*/
.wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 0 auto;
  overflow: hidden;
}

.header {
  position: relative;
  display: flex;
  min-height: 65px;
  padding-top: 10px;
}
@media (min-width: 768px) {
  .header {
    width: auto;
    padding-top: 0;
    padding-bottom: 10px;
  }
}
@media (min-width: 1024px) {
  .header {
    padding-top: 53px;
    padding-bottom: 32px;
  }
}
.header::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: -20px;
  width: 100vw;
  min-width: 320px;
  height: 1px;
  background-color: #e6e6e6;
}
@media (min-width: 768px) {
  .header::before {
    display: none;
  }
}
.header.no-js {
  display: block;
}
@media (min-width: 768px) {
  .header.no-js {
    display: flex;
  }
}

.header__logo-link {
  display: flex;
  justify-content: space-between;
  width: calc(50% + 52px);
  margin-bottom: 17px;
  text-decoration: none;
  pointer-events: none;
}
@media (min-width: 768px) {
  .header__logo-link {
    width: auto;
    margin-right: 96px;
    margin-bottom: 0;
  }
}
.header__logo-link:hover {
  opacity: 0.8;
}
.header__logo-link:active, .header__logo-link:focus {
  opacity: 0.6;
}

.header__logo-image {
  pointer-events: all;
}
@media (min-width: 768px) {
  .header__logo-image {
    width: 174px;
    height: 52px;
  }
}
@media (min-width: 1024px) {
  .header__logo-image {
    width: 202px;
    height: 59px;
  }
}
@media (min-width: 768px) {
  .header__logo-image--text {
    display: none;
  }
}

.header__nav-toggle {
  display: flex;
  width: 30px;
  height: 30px;
  margin-left: auto;
  padding: 5px 3px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.no-js .header__nav-toggle {
  display: none;
}
@media (min-width: 768px) {
  .header__nav-toggle {
    display: none;
  }
}

.header__toggle-line {
  position: relative;
  display: block;
  align-self: center;
  width: 100%;
  height: 2px;
  background-color: #000000;
}
.header__toggle-line::before, .header__toggle-line::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #000000;
}
.header__toggle-line::before {
  top: -8px;
}
.header__toggle-line::after {
  top: 8px;
}
.is-open .header__toggle-line {
  background-color: transparent;
}
.is-open .header__toggle-line::before, .is-open .header__toggle-line::after {
  top: 0;
  left: 50%;
  width: 26px;
}
.is-open .header__toggle-line::before {
  transform: translateX(-50%) rotate(45deg);
}
.is-open .header__toggle-line::after {
  transform: translateX(-50%) rotate(-45deg);
}

@media (min-width: 768px) {
  .header__nav {
    display: flex;
    max-width: calc(100% - 268px);
    margin-left: auto;
    padding-top: 8px;
  }
}
@media (min-width: 1024px) {
  .header__nav {
    width: 50%;
    max-width: none;
    padding-top: 16px;
    padding-left: 15px;
  }
}

.header__nav-list {
  position: absolute;
  top: 65px;
  left: -20px;
  z-index: 3;
  display: none;
  width: calc(100% + 40px);
  margin: 0;
  padding: 0;
  list-style: none;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  font-family: "Oswald", "Arial", sans-serif;
  color: #000000;
  text-transform: uppercase;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .header__nav-list {
    position: static;
    display: flex;
    flex-wrap: wrap;
    width: auto;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    column-gap: 34px;
    background-color: transparent;
    row-gap: 10px;
  }
}
@media (min-width: 1024px) {
  .header__nav-list {
    margin-left: auto;
    font-size: 20px;
    line-height: 30px;
    column-gap: 7%;
    min-width: 477px;
  }
}
.no-js .header__nav-list {
  position: static;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .no-js .header__nav-list {
    display: flex;
  }
}
.is-open .header__nav-list {
  display: block;
}
@media (min-width: 768px) {
  .is-open .header__nav-list {
    position: static;
    display: grid;
  }
}
@media (min-width: 1024px) {
  .header__nav-list--index {
    color: #ffffff;
  }
}

.header__nav-item {
  position: relative;
  text-align: center;
}
.header__nav-item::before {
  content: "";
  position: absolute;
  top: 0;
  left: -20px;
  width: calc(100% + 40px);
  height: 1px;
  background-color: #e6e6e6;
}
@media (min-width: 768px) {
  .header__nav-item::before {
    display: none;
  }
}

.header__nav-link {
  display: block;
  padding: 22px 20px 24px 20px;
  color: inherit;
  text-decoration: none;
}
@media (min-width: 768px) {
  .header__nav-link {
    padding: 10px 0;
  }
}
.header__nav-link:hover {
  opacity: 0.6;
}
.header__nav-link:focus {
  opacity: 0.8;
}
@media (min-width: 768px) {
  .header__nav-link:active {
    opacity: 1;
    border-bottom: 2px solid #68b738;
  }
}
@media (min-width: 1024px) {
  .header__nav-link:active {
    opacity: 1;
    border-color: #ffffff;
  }
}
.header__nav-link:disabled {
  opacity: 0.3;
  pointer-events: none;
}
.header__nav-link--current {
  pointer-events: none;
}
@media (min-width: 768px) {
  .header__nav-link--current {
    border-bottom: 2px solid #68b738;
  }
}
@media (min-width: 1024px) {
  .header__nav-link--current {
    border-color: #ffffff;
  }
}

